/* General styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
    color: #333;
}

/* Dashboard container */
.dashboard-container {
    max-width: 600px;
    margin: 3rem auto;
    background: #fff;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
}

/* Heading */
.dashboard-container h1 {
    color: #4CAF50;
    font-size: 2rem;
    margin-bottom: 1rem;
}

/* Paragraph text */
.dashboard-container p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    color: #666;
}

/* Button styles */
.dashboard-container button {
    padding: 0.8rem 1.5rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.dashboard-container button:hover {
    background-color: #45a049;
}

.dashboard-container button:active {
    background-color: #3e8e41;
}
